import React from 'react';
import ReactDOM from 'react-dom';
import { EditorContextHelper, TemplateAnnotations } from '@magnolia/template-annotations';

const context = {};
const inEditor = EditorContextHelper.inEditor();

function insertComments(node, commentOpen, commentClose) {
  if (node) {
    node.parentNode.insertBefore(document.createComment(commentOpen), node);
    node.parentNode.insertBefore(document.createComment(commentClose), node.nextSibling);
  }
}

function setContextProp(prop) {
  const { key, value } = prop;

  if (!value) return console.error('Missing prop: ' + key);

  context[key] = value;
}

function getComponentMapping(node) {
  const key = node['mgnl:template'];
  const value = context.config.componentMappings[key];

  return value ? value : console.error('Missing component mapping for: ' + key);
}

class EditablePage extends React.PureComponent {
  componentDidMount() {
    if (inEditor) {
      const { content } = this.props;
      const templateDefinition = context.templateDefinitions[content['mgnl:template']];
      const commentOpen = TemplateAnnotations.getPageCommentString(content, templateDefinition);

      insertComments(ReactDOM.findDOMNode(this), commentOpen, '/cms:page');
    }
  }

  render() {
    const { content = {}, config, templateDefinitions } = this.props;

    setContextProp({ key: 'config', value: config });

    if (inEditor) setContextProp({ key: 'templateDefinitions', value: templateDefinitions });

    const component = getComponentMapping(content);

    return component ? React.createElement(component, content) : null;
  }
}

