class EditableComponent extends React.PureComponent {
  componentDidMount() {
    if (inEditor) {
      const { content } = this.props;
      const templateDefinition = this.templateDefinitions[content['mgnl:template']];
      const commentOpen = TemplateAnnotations.getComponentCommentString(content, templateDefinition);

      insertComments(ReactDOM.findDOMNode(this), commentOpen, '/cms:component');
    }
  }

  render() {
    const props = this.props;
    const component = getComponentMapping(props);

    return component ? React.createElement(component, props) : null;
  }
}

export { EditablePage, EditableArea, EditableComponent };
