import React from 'react';
import { EditableArea } from '../react-editor';

function Home(props) {
  const { title, main, header } = props;

  return (
    <div>
      <h1>{title}</h1>
      {header && <EditableArea content={header} />}
      {main && <EditableArea content={main} />}
    </div>
  );
}

export default Home;
